@use '../utilities/variables' as *;
@use '../utilities/functions' as *;
@use '../utilities/mixins' as *;
@use 'sass:color';
@forward '../helpers/typography';

.default-success {
  color: #fff;
  background-color: #0f1c24 !important;
}

.form-control {
  background-color: white;
  border: 1px solid pick-color('primary', 'fog');
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 13px;
  color: pick-color('primary', 'charcoal');
  box-shadow: none;

  &::placeholder {
    color: pick-color('primary', 'ash');
  }
}

input::placeholder {
  color: pick-color('primary', 'ash');
}

label {
  flex-wrap: nowrap;
  font-weight: 400;
  color: pick-color('primary', 'charcoal');
  font-size: 14px;
  margin-bottom: 6px;
}

input[type='checkbox'] {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  -o-appearance: none;
  border: 1px solid color.adjust(pick-color('primary', 'fog'), $blackness: 10%);
  background-color: white;
  padding: 5px;
  border-radius: 3px;
  position: relative;
  margin-right: 6px;
  width: 18px;
  height: 18px;
  display: flex;
  flex-shrink: 0;

  &:checked {
    background-color: pick-color('primary', 'web');
    position: relative;
    border-color: pick-color('primary', 'web');

    &:before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-family: 'Material Icons';
      content: 'done';
      font-size: 18px;
      color: white;
    }
  }
}

@keyframes shimmer {
  to {
    background-position-x: 0%;
  }
}

.shimmer {
  background: linear-gradient(-45deg, #eee 40%, #fafafa 50%, #eee 60%);
  background-size: 300%;
  background-position-x: 100%;
  animation: shimmer 1s infinite linear;
}

mat-tooltip-component {
  .mdc-tooltip.mat-mdc-tooltip {
    max-width: 400px;

    .mat-mdc-tooltip-surface.mdc-tooltip__surface {
      padding: 8px 16px;
      background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 40%);
      border-radius: 6px;
      color: pick-color('primary', 'blue');
      font-size: 13px;
      font-weight: 500;
      max-width: unset;
    }
  }
}

.dropdown-wrapper {
  display: none;
  position: absolute;
  z-index: 50;
  width: 100%;
  margin: 6px 0 0 0;
  top: 90%;
  left: 0;

  &.show {
    display: block;
  }

  .dropdown {
    min-width: 100%;
    width: fit-content;
    border: 1px solid color.adjust(pick-color('primary', 'blue'), $lightness: 45%);
    max-height: 300px;
    overflow: hidden;
    overflow-y: auto;
    background-color: white;
    border-radius: 6px;
    margin-top: 2px;

    li {
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;
      padding: 10px 20px;
      width: 100%;
      color: pick-color('primary', 'charcoal');
      line-height: 20px;
      font-size: 14px;
      white-space: nowrap;

      .radio {
        display: flex;
        flex-shrink: 0;
        border: 1px solid pick-color('primary', 'ash');
        width: 14px;
        height: 14px;
        border-radius: 50px;
        margin-right: 6px;
      }

      .check {
        display: flex;
        flex-shrink: 0;
        border: 1px solid pick-color('primary', 'ash');
        width: 14px;
        height: 14px;
        border-radius: 4px;
        margin-right: 6px;
      }

      &.selected {
        .radio {
          background-color: pick-color('primary', 'web');
          position: relative;
          border: none;

          &::before {
            content: '';
            position: absolute;
            width: 7px;
            height: 7px;
            background-color: white;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: inherit;
          }
        }

        .check {
          background-color: pick-color('primary', 'web');
          position: relative;
          border: none;

          &::before {
            font-family: 'Material Icons';
            content: 'done';
            position: absolute;
            top: 50%;
            left: 50%;
            color: white;
            transform: translate(-50%, -50%);
            border-radius: inherit;
          }
        }
      }
    }
  }
}

.all-filters-dialog {
  margin-top: 20px;
  align-self: flex-start;
}

.session-card,
.file-card {
  display: grid;
  grid-template-columns: 200px 220px 300px 60px 60px 200px 120px;
  grid-template-rows: 1fr;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  margin-left: 6px;
  padding: 18px 16px;
  background-color: white;
  width: 100%;
  border-radius: inherit;

  span {
    font-weight: 500;
    color: pick-color('primary', 'charcoal');
    line-height: 18px;
    font-size: 14px;
    white-space: nowrap;
  }

  .sc-creator {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .sc-cal,
  .fc-cal {
    display: flex;
    align-items: center;
    gap: 8px;

    span {
      color: pick-color('primary', 'ash');
    }

    .timing-date {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .sc-timing,
  .fc-timing {
    display: flex;
    align-items: center;
    gap: 8px;

    .timing-icon {
      display: flex;
      color: pick-color('primary', 'web');

      mat-icon {
        width: 20px;
        height: 20px;
        font-size: 20px;
      }
    }

    .timing-duration {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .sc-location,
  .fc-location {
    display: flex;
    align-items: center;
    cursor: pointer;

    .location-label {
      margin-left: 2px;
    }
  }

  .sc-category {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .sc-additional-icons {
    display: flex;
    align-items: center;
    justify-self: center;
    column-gap: 16px;

    .rounded-icon {
      padding: 4px;
      @include circles(30px, pick-color('primary', 'web'), white);
      cursor: pointer;

      svg {
        width: 20px;
        height: 20px;
        flex-shrink: 0;
      }

      &.icon-overflow {
        svg {
          width: 18px;
          height: 18px;
        }
      }
    }
  }

  .sc-dropdown-toggle-wrapper {
    justify-self: flex-end;

    span {
      font-weight: 400;
    }

    .dropdown-toggle-btn {
      @include circles(32px, color.adjust(pick-color('primary', 'blue'), $lightness: 46%));
      cursor: pointer;
      transition: all 0.2s ease;

      .toggle-icon {
        width: 20px;
        height: 20px;
        font-size: 20px;
        transition: all 0.2s ease;
        color: pick-color('primary', 'blue');
      }

      &.toggled {
        background-color: pick-color('primary', 'web');

        .toggle-icon {
          transform: rotate(-180deg);
          color: white;
        }
      }
    }
  }
}
