@use '../utilities/variables' as *;

// Fill
$fill-values: none, inherit, 'transparent';

@each $name in $fill-values {
  .theme-fill-#{$name} {
    fill: $name !important;
  }
}

@each $code, $inner-map in $mview-colors {
  @each $name, $color in $inner-map {
    .text-#{$code}-#{$name} {
      color: $color !important;
    }

    .bg-#{$code}-#{$name} {
      background-color: $color !important;
    }

    .border-#{$code}-#{$name} {
      border-color: $color !important;
    }

    .fill-#{$code}-#{$name} {
      fill: $color !important;
    }
  }
}
