@use 'sass:map';
@use '../utilities/variables' as *;
@use '../utilities/functions' as *;
@use '../utilities/mixins' as *;
@forward '../helpers/typography';

html {
  font-size: map.get($font-sizes, 'normal');
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
button,
input,
optgroup,
select,
textarea {
  @extend .ft-default;
}

