html,
body {
  height: 100%;
}

body {
  font-family: Roboto, 'Helvetica Neue', sans-serif;
  background-color: pick-color('common', 'background');
  // background-color: white;
}

article,
aside,
figure,
footer,
header,
hgroup,
section {
  display: block;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.pointer {
  cursor: pointer;
}

.underline {
  text-decoration: underline;
}

.no-event {
  pointer-events: none;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 5px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: #d8d8d8;
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

.content {
  padding: 0 0.5rem;
}

input {
  &:focus {
    outline: none;
  }
}

button {
  background-color: transparent;
  outline: none;
  border: none;
}

.btn {
  background-color: transparent;
  outline: none;
  box-shadow: none;
}

.btn:disabled,
.btn-disabled {
  cursor: not-allowed !important;
  pointer-events: all;
  opacity: 0.4 !important;
  user-select: none;
  border-color: transparent;
}

.cursor-grab {
  cursor: grab !important;
}

.cursor-grabbing {
  cursor: grabbing !important;
}

.cursor-resize {
  cursor: ew-resize !important;
}

.cursor-crosshair {
  cursor: crosshair;
}
