@use 'sass:list';
@use '../utilities/variables' as *;
@use '../utilities/functions' as *;

// head sizes
@each $value in $head-sizes {
  $i: list.index($head-sizes, $value);
  .h#{$i},
  h#{$i} {
    font-size: #{$value};
  }
}

@for $i from 16 through 28 {
  .lh-#{$i} {
    line-height: #{$i}px !important;
  }
}

// Font size
@each $key, $value in $font-sizes {
  .text-#{$key} {
    font-size: $value;
  }
}

.text-disabled {
  color: pick-common-color(slate, 300);
}

// Font weight
@each $key, $value in $font-weights {
  .#{$key} {
    font-weight: #{$value};
  }
}

.ft-default {
  font-family: Roboto, 'Helvetica Neue', sans-serif;
  font-weight: 400;
}

// Alignment
$alignments: 'center', 'justify', 'left', 'right';

@each $value in $alignments {
  .text-#{$value} {
    text-align: #{$value} !important;
  }
}
