mat-list-option[ng-reflect-selected='true'] {
  color: white;
  background: $mat-color-accent;
  border-radius: 10px;

  .mdc-list-item__content .heading-color {
    color: white;
  }

  .with-icon {
    color: white;
  }
}

.mat-mdc-list-item,
.mat-mdc-list-option {
  width: 100%;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.mdc-list-item__end {
  display: none;
}

.mat-expansion-indicator {
  display: none !important;
}

.mat-expansion-panel-header-title {
  color: $mat-color-accent !important;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 19px;
  font-weight: 500;
}

.mat-expansion-panel-header-title span {
  height: 18px;
  width: 18px;
  color: white;
  border-radius: 50%;
  background: $mat-color-accent;
  margin-right: 14px;
  text-align: center;
}

.mat-expansion-panel-header-title .mat-icon {
  font-size: 17px;
}

.mat-form-field-invalid {
  border-color: $mat-color-danger !important;
}

.mat-mdc-form-field-error {
  color: $mat-color-danger !important;
}

.mat-mdc-slide-toggle.mat-accent {
  --mdc-switch-selected-focus-state-layer-color: #579cc5;
  --mdc-switch-selected-handle-color: #0f72ad;
  --mdc-switch-selected-hover-state-layer-color: #579cc5;
  --mdc-switch-selected-pressed-state-layer-color: #579cc5;
  --mdc-switch-selected-focus-handle-color: #0f72ad;
  --mdc-switch-selected-hover-handle-color: #0f72ad;
  --mdc-switch-selected-pressed-handle-color: #0f72ad;
  --mdc-switch-selected-focus-track-color: #579cc5;
  --mdc-switch-selected-hover-track-color: #579cc5;
  --mdc-switch-selected-pressed-track-color: #579cc5;
  --mdc-switch-selected-track-color: #579cc5;
}

.mat-expansion-panel:not([class*='mat-elevation-z']) {
  box-shadow: none !important;
}

.mat-mdc-dialog-surface.mdc-dialog__surface {
  overflow: visible;
  border-radius: 10px !important;
}
