@use 'sass:map';
@use '../utilities/variables' as *;

// Width
$width-values: map.merge(
  $property-values,
  (
    'screen': 100vw,
    'auto': auto,
    'min': min-content,
    'max': max-content,
    'fit': fit-content
  )
) !default;

@each $name, $value in $width-values {
  .w-#{$name} {
    width: $value !important;
  }
}

@each $name, $value in $property-percentage-values {
  .w-p-#{$name} {
    width: $value !important;
  }
}

//min width
$min-width-values: (
  '0': 0,
  'full': 100%,
  'min': min-content,
  'max': max-content,
  'fit': fit-content
) !default;

@each $name, $value in $min-width-values {
  .min-w-#{$name} {
    min-width: $value !important;
  }
}

//max width
$max-width-values: map.merge(
  $property-values,
  (
    'none': none,
    'md': 28rem,
    'lg': 32rem,
    'xl': 36rem,
    'hd': 1240px,
    'full': 100%,
    'min': min-content,
    'max': max-content,
    'fit': fit-content
  )
) !default;

@each $name, $value in $max-width-values {
  .max-w-#{$name} {
    max-width: $value !important;
  }
}

//height
$height-values: map.merge(
  $property-values,
  (
    'screen': 100vh,
    'auto': auto,
    'min': min-content,
    'max': max-content,
    'fit': fit-content
  )
) !default;

@each $name, $value in $height-values {
  .h-#{$name} {
    height: $value !important;
  }
}

@each $name, $value in $property-percentage-values {
  .h-p-#{$name} {
    height: $value !important;
  }
}

//min height
$min-height-values: (
  '0': 0,
  'full': 100%,
  'screen': 100vh,
  'min': min-content,
  'max': max-content,
  'fit': fit-content
) !default;

@each $name, $value in $min-height-values {
  .min-h-#{$name} {
    min-height: $value !important;
  }
}

//max height
$max-height-values: map.merge(
  $property-values,
  (
    'none': none,
    'full': 100%,
    'screen': 100vh,
    'min': min-content,
    'max': max-content,
    'fit': fit-content
  )
) !default;

@each $name, $value in $max-height-values {
  .max-h-#{$name} {
    max-height: $value !important;
  }
}
