@use 'sass:math';
@use 'sass:map';
@use '../utilities/variables' as *;
@use '../utilities/functions' as *;
@use '../utilities/mixins' as *;

$shadow-color: pick-color('primary', 'fog');

// Shadows
.shadow-normal {
  box-shadow: 0 2px 3px $shadow-color !important;
}

.shadow-fancy {
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.05) !important;
}

.shadow-inner {
  box-shadow: inset black 0px 0px 12px -12px !important;
}

.shadow-none {
  box-shadow: none !important;
}

// Rounded borders
.rounded-sm {
  @include border-radius(4px);
}

.rounded {
  @include border-radius(6px);
}

.rounded-md {
  @include border-radius(8px);
}

.rounded-lg {
  @include border-radius(10px);
}

.rounded-xl {
  @include border-radius(15px);
}

.rounded-full {
  @include border-radius(50px);
}

.rounded-none {
  @include border-radius(0px);
}

// Opacity
@for $i from 0 through 10 {
  .opacity-#{$i} {
    opacity: math.div($i, 10);
  }
}

// Position
$position-values: map.merge(
  $property-values,
  (
    'auto': auto
  )
) !default;

.static {
  position: static !important;
}

.fixed {
  position: fixed;
}

.absolute {
  position: absolute;
}

.relative {
  position: relative;
}

.sticky {
  position: sticky;
}

@each $name, $value in $position-values {
  .top-#{$name} {
    top: $value;
  }

  .right-#{$name} {
    right: $value;
  }

  .bottom-#{$name} {
    bottom: $value;
  }

  .left-#{$name} {
    left: $value;
  }
}

@each $name, $value in $property-percentage-values {
  .top-p-#{$name} {
    top: $value;
  }

  .right-p-#{$name} {
    right: $value;
  }

  .bottom-p-#{$name} {
    bottom: $value;
  }

  .left-p-#{$name} {
    left: $value;
  }
}

// z-index
@for $i from 1 through 100 {
  .z-index-#{$i} {
    z-index: $i;
  }
}

.translate-middle {
  transform: translate(-50%, -50%);
}
